import DropPanel    from './components/DropPanel';
import MainMenu     from './components/MainMenu';
import fIle         from './components/addFIle';
import burger       from './components/burger';
import tab          from './components/Tabs';
import initSelects  from './components/select';
import smoothscorll from './components/smoothscorll';
import accordion    from './components/accordion';
import popupsFunc   from './components/modals';
import valid        from './components/validate';

// Init
function init() {
	new DropPanel();
	tab();
	fIle();
	burger();
	initSelects();
	smoothscorll();
	accordion();
	valid();
	popupsFunc.init();
	const appHeight = () => {
		const doc = document.documentElement;
		doc.style.setProperty('--app-height', `${window.innerHeight}px`);
	};
	window.addEventListener('resize', appHeight);
	appHeight();
}

(function () {
	init();
}());
