const fIle = () => {
	const inputFile = document.querySelector('.add-form__input');
	const button    = document.querySelector('.add-form__label');
	const theReturn = document.querySelector('.file-return');
	const btn       = document.getElementById('file-btn');

	if (inputFile) {
		button.addEventListener('click', (event) => {
			inputFile.focus();
			return false;
		});
		inputFile.addEventListener('change', function (event) {
			theReturn.innerHTML = this.files[0].name;
			btn.classList.remove('disabled');
		});
	}
};
export default fIle;
